* {
  padding: 0;
  margin: 0;
  // background-color: #f3f4f5;#f3f4f5
  // box-sizing: border-box;
  //overflow-x: hidden;
}
.App {
  margin: 0;
}

.App-content {
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  flex-direction: column;
  padding: 0;

  // min-height: 100vh;
}

.header {
  position: relative;
  top: 0;
  background-color: #fdfea0;
  overflow: hidden;
}

.section2 {
  position: relative;
  // height: 100vh;
  // background-color: #16305a;
    min-height: 100vh;
    height: 100vh;
    display: flex;
    align-items: center;
    border-bottom: 0px;
    background: #16305a;
    font-weight: 400;
    color: #d1e2ff;
    padding: 0rem 5.6rem;
    margin-bottom: 0;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
}
.container-text{
  max-width: 640px;
  width: 100%;
  padding-right: .75rem;
  padding-left: .75rem;
  margin-right: auto;
  margin-left: auto;
}
.title{
  font-weight: bolder;
  font-size: 3.6rem;
  text-align: center;
}
.s1-svg {
  position: absolute;
  top: -4px;
  left: 0;
  width: 100%;
  transform: rotate(180deg);
  z-index: 3;
}
