.section3 {
  position: relative;
  height: 100vh;
  background-color: rgb(201, 248, 165);
}
.section4 {
  position: relative;
  background-color: rgb(158, 197, 236);
  min-height: 100vh;
}
.svg {
  overflow: hidden;
  position: absolute;
  bottom: -2px;
  padding-bottom: 0;
  margin-bottom: 0;
  width: 100%;
}
.wave {
  transform: rotate(180deg);
}

.footer {
  position: relative;
  width: 100%;
  color: rgb(232, 241, 249);
  font-weight: bolder;
